
/*CSS from https://scotch.io/tutorials/build-an-infinite-scroll-image-gallery-with-react-css-grid-and-unsplash-solution-to-code-challenge-16*/

.container {
    margin-top: 50px;
    text-align: center;
}

.image-grid {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: minmax(50px, auto);

}

.image-item:nth-child(5n){
    grid-column-end: span 2;
}

.image-grid img{
    display: flex;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

